import styled from 'styled-components';

export const Container = styled.div`
    position: absolute;
    left: 0px;
    width: 400px;
    height: 400px;
    margin-top: 680px;
    margin-left: 200px;
    border: 3px solid black;
    border-radius: 10px;
    margin-bottom: 50px;
`;
export const Topic = styled.div`
    //border: 2px solid red;
    background-color: black;
    color: white;
    font-size: 25px;
    padding: 10px;
    text-align: center;
    border-radius: 5px;
    letter-spacing: 2px;
`;
export const Info = styled.div`
    color: black;
    font-size: 20px;
    padding-bottom: 5px;
    letter-spacing: 1px;
    word-spacing: 2px;
    font-weight: bold;
`;
export const SubInfo = styled.div`
    color: black;
    font-size: 18px;
    padding-bottom: 10px;
    letter-spacing: 1px;
    word-spacing: 1px;
    text-align: justify;
`;
export const InfoBox = styled.div`
    padding-right: 25px;
`;


